<template>
  <div class="updateContainer">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/company' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="form">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="220px"
        class="demo-ruleForm"
      >
        <div class="first">
          <el-form-item label="" prop="bname">
            <el-input v-model="ruleForm.bname" placeholder="20"></el-input>
          </el-form-item>
          <el-form-item label="" prop="mainAccount">
            <el-input v-model="ruleForm.mainAccount" placeholder="3-10" :disabled="true"></el-input>
          </el-form-item>
          <p class="ptips"></p>
          <el-form-item label="" prop="address">
            <!--  -->
            <label>
              <el-cascader
                v-model="ruleForm.area"
                :options="areaOption"
                @change="handleChange"
                clearable
              ></el-cascader>
            </label>
            <el-input
              v-model="ruleForm.address"
              placeholder=""
              @input="inputOnInput($event)"
              id="suggestId"
            ></el-input>
          </el-form-item>
          <el-form-item style="marginTop:-15px;">
            <baidu-map
              :center="center"
              :zoom="zoom"
              @ready="handler"
              class="baiduMap"
              :scroll-wheel-zoom="true"
              id="l-map"
            >
              <bm-local-search :keyword="arealocation" :auto-viewport="true" :panel="false"></bm-local-search>
              <bm-local-search
                :keyword="ruleForm.address"
                :auto-viewport="true"
                :panel="false"
                @searchcomplete="getGps"
              ></bm-local-search>
            </baidu-map>
          </el-form-item>
          <el-form-item label="" prop="personInCharge">
            <el-input v-model="ruleForm.personInCharge" placeholder="20"></el-input>
          </el-form-item>
          <el-form-item label="" prop="picPhone">
            <el-input v-model="ruleForm.picPhone"></el-input>
          </el-form-item>
          <el-form-item label="" prop="validityTime">
            <el-date-picker
              v-model="ruleForm.validityTime"
              type="date"
              placeholder=""
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="ruleForm.status" placeholder="" @change="sta">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input
              type="textarea"
              v-model="ruleForm.description"
              placeholder="500"
              maxlength="500"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="deviceNumLimit">
            <el-input v-model="ruleForm.deviceNumLimit" @change="limitChange"></el-input>
          </el-form-item>
        </div>
        <div class="second">
          <el-form-item label="" required>
            <div class="checkGroup">
              <el-checkbox label="" v-model="ruleForm.voice" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.see" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.talk" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.rail" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.track" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.broad" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="" v-model="ruleForm.detect" true-label="0" false-label="1"></el-checkbox>
            </div>
          </el-form-item>
          <el-form-item label="" prop="time" required>
            <el-radio-group v-model="ruleForm.time" @change="finalTime">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input
              :disabled="one"
              v-model="ruleForm.timeLimit"
              @change="inputOne"
              @blur="decimalOne"
            ></el-input>
            <span></span>
            <i>1440</i>
          </el-form-item>
          <el-form-item label="" prop="upload" required>
            <el-radio-group v-model="ruleForm.upload" @change="videoUpload">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input
              :disabled="two"
              v-model="ruleForm.videoLimit"
              @change="inputTwo"
              @blur="decimalTwo"
            ></el-input>
            <span>MB</span>
            <i></i>
          </el-form-item>
          <el-form-item label="app" prop="app" required>
            <el-radio-group v-model="ruleForm.app" @change="appUpload">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input
              :disabled="three"
              v-model="ruleForm.appLimit"
              @change="inputThree"
              @blur="decimalThree"
            ></el-input>
            <span>MB</span>
            <i>wifiwifi</i>
          </el-form-item>
          <el-form-item label="" prop="switch" required>
            <el-radio-group v-model="ruleForm.switch" @change="logSwitch">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input
              :disabled="four"
              v-model="ruleForm.switchLimit"
              @change="inputFour"
              @blur="decimalFour"
            ></el-input>
            <span></span>
            <i>550</i>
          </el-form-item>
          <el-form-item label="app" prop="log" required>
            <el-radio-group v-model="ruleForm.log" @change="appLog">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input
              :disabled="five"
              v-model="ruleForm.appLogLimit"
              @change="inputFive"
              @blur="decimalFive"
            ></el-input>
            <span></span>
            <i>550</i>
          </el-form-item>
          <el-form-item label="" prop="save" required>
            <el-radio-group v-model="ruleForm.save" @change="savation">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
            <el-input
              :disabled="six"
              v-model="ruleForm.saveLimit"
              @change="inputSix"
              @blur="decimalSix"
            ></el-input>
            <span>GB</span>
            <i></i>
          </el-form-item>
        </div>
        <el-form-item>
          <el-button type="primary" @click="update"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
import { BmlMarkerClusterer } from "vue-baidu-map";
const { mapState, mapActions } = createNamespacedHelpers("company");
export default {
  data() {
    return {
      ruleForm: {
        bname: "",
        mainAccount: "",
        area: "",
        address: "",
        personInCharge: "",
        picPhone: "",
        status: "",
        description: "",
        deviceNumLimit: 100,
        validityTime: "",
        voice: 1,
        see: 1,
        talk: 1,
        rail: 1,
        track: 1,
        broad: 1,
        detect: 1,
        time: "",
        timeLimit: 0,
        upload: "",
        videoLimit: 0,
        app: "",
        appLimit: 0,
        switch: "",
        switchLimit: 0,
        log: "",
        appLogLimit: 0,
        save: "",
        saveLimit: 0,
      },
      lons: "",
      lats: "",
      areaOption: regionData,
      one: true,
      two: true,
      three: true,
      four: true,
      five: true,
      six: true,
      center: { lng: 104, lat: 30 },
      zoom: 13,
      location: "",
      keyword: "",
      arealocation: "",
      rules: {
        bname: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 20, message: "20", trigger: "blur" },
        ],
        mainAccount: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 10,
            message: "3-10",
            trigger: "blur",
          },
        ],
        address: [
          { required: true, message: "", trigger: "blur" },
        ],
        personInCharge: [
          { required: true, message: "", trigger: "blur" },
        ],
        picPhone: [
          { required: true, message: "", trigger: "blur" },
        ],
        validityTime: [
          { required: true, message: "", trigger: "blur" },
        ],
        status: [
          { required: true, message: "", trigger: "blur" },
        ],
        description: [{ max: 500, message: "500", trigger: "blur" }],
        deviceNumLimit: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
    };
  },
  async mounted() {
    let id = JSON.parse(sessionStorage.getItem("updateID"));
    const data = await this.companyDetailAsync(id);
    // console.log(data);
    this.allLimits = data.businessManager.deviceNumLimit;
    this.did = data.diySettingMapper.did;
    let company = Object.assign(data.businessManager, data.diySettingMapper);
    let newfrom = Object.assign(this.ruleForm, company);
    this.setlongitude = company.longitude;
    this.setlatitude = company.latitude;
    this.ruleForm.address = company.address;
    //
    if (company.status == 0) {
      this.ruleForm.status = "";
    } else if (company.status == 1) {
      this.ruleForm.status = "";
    }
    this.setStatus = data.businessManager.status;
    // console.log(this.setStatus);

    //
    if (company.isCall == "0") {
      this.ruleForm.voice = "0";
    } else {
      this.ruleForm.voice = "";
    }
    if (company.vedio == "0") {
      this.ruleForm.see = "0";
    } else {
      this.ruleForm.see = "";
    }
    if (company.talkback == "0") {
      this.ruleForm.talk = "0";
    } else {
      this.ruleForm.talk = "";
    }
    if (company.mapRange == "0") {
      this.ruleForm.rail = "0";
    } else {
      this.ruleForm.rail = "";
    }
    if (company.trail == "0") {
      this.ruleForm.track = "0";
    } else {
      this.ruleForm.track = "";
    }
    if (company.broadcast == "0") {
      this.ruleForm.broad = "0";
    } else {
      this.ruleForm.broad = "";
    }
    if (company.listener == "0") {
      this.ruleForm.detect = "0";
    } else {
      this.ruleForm.detect = "";
    }
    //
    if (company.vedioTimeLimitSys == 0) {
      this.ruleForm.time = "";
    } else if (company.vedioTimeLimitSys == 1) {
      this.ruleForm.time = "";
    } else if (company.vedioTimeLimitSys == 2) {
      this.ruleForm.time = "";
      this.ruleForm.timeLimit = company.vedioTimeLimit;
      this.one = false;
    }
    this.setVedioTimeLimitSys = data.diySettingMapper.vedioTimeLimitSys;
    this.setVedioTimeLimit = data.diySettingMapper.vedioTimeLimit;
    //
    if (company.isAutoUploadSys == 0) {
      this.ruleForm.upload = "";
    } else if (company.isAutoUploadSys == 1) {
      this.ruleForm.upload = "";
    } else if (company.isAutoUploadSys == 2) {
      this.ruleForm.upload = "";
      this.ruleForm.videoLimit = company.isAutoUpload;
      this.two = false;
    }
    this.setIsAutoUploadSys = data.diySettingMapper.isAutoUploadSys;
    this.setIsAutoUpload = data.diySettingMapper.isAutoUpload;
    //app
    if (company.dispatcherUploadLimitSys == 0) {
      this.ruleForm.app = "";
    } else if (company.dispatcherUploadLimitSys == 1) {
      this.ruleForm.app = "";
    } else if (company.dispatcherUploadLimitSys == 2) {
      this.ruleForm.app = "";
      this.ruleForm.appLimit = company.dispatcherUploadLimit;
      this.three = false;
    }
    this.setDispatcherUploadLimitSys =
      data.diySettingMapper.dispatcherUploadLimitSys;
    this.setDispatcherUploadLimit = data.diySettingMapper.dispatcherUploadLimit;
    //
    if (company.appLogSys == 0) {
      this.ruleForm.switch = "";
    } else if (company.appLogSys == 1) {
      this.ruleForm.switch = "";
    } else if (company.appLogSys == 2) {
      this.ruleForm.switch = "";
      this.ruleForm.switchLimit = company.appLog;
      this.four = false;
    }
    this.setAppLogSys = data.diySettingMapper.appLogSys;
    this.setAppLog = data.diySettingMapper.appLog;
    //app
    if (company.dispatcherLogSys == 0) {
      this.ruleForm.log = "";
    } else if (company.dispatcherLogSys == 1) {
      this.ruleForm.log = "";
    } else if (company.dispatcherLogSys == 2) {
      this.ruleForm.log = "";
      this.ruleForm.appLogLimit = company.dispatcherLog;
      this.five = false;
    }
    this.setDispatcherLogSys = data.diySettingMapper.dispatcherLogSys;
    this.setDispatcherLog = data.diySettingMapper.dispatcherLog;
    //
    if (company.storeTipSys == 0) {
      this.ruleForm.save = "";
    } else if (company.storeTipSys == 1) {
      this.ruleForm.save = "";
      this.ruleForm.saveLimit = company.storeTip;
      this.six = false;
    }
    this.setStoreTipSys = data.diySettingMapper.storeTipSys;
    this.setStoreTip = data.diySettingMapper.storeTip;
  },
  methods: {
    ...mapActions(["updateCompanyAsync", "formAsync", "companyDetailAsync"]),
    handleChange(value) {
      let province = CodeToText[value[0]];
      let city = CodeToText[value[1]];
      let area = CodeToText[value[2]];
      this.location = province + city + area;
      this.ruleForm.address = this.location;
      this.changelocationflag = true;
    },
    //
    sta() {
      this.staus = this.ruleForm.status;
      this.statusFlag = true;
    },
    //
    finalTime() {
      if (this.ruleForm.time == "") {
        this.videoTime = 0;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "") {
        this.videoTime = 1;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "") {
        this.videoTime = 2;
        this.one = false;
      }
      this.oneFlag = true;
      console.log(this.ruleForm.timeLimit);
    },
    //
    videoUpload() {
      console.log(this.ruleForm.upload);
      if (this.ruleForm.upload == "") {
        this.uploadVideo = 0;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "") {
        this.uploadVideo = 1;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "") {
        this.uploadVideo = 2;
        this.two = false;
      }
      this.twoFlag = true;
    },
    //app
    appUpload() {
      if (this.ruleForm.app == "") {
        this.appVideo = 0;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "") {
        this.appVideo = 1;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "") {
        this.appVideo = 2;
        this.three = false;
      }
      this.threeFlag = true;
    },
    //
    logSwitch() {
      if (this.ruleForm.switch == "") {
        this.switchLog = 0;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "") {
        this.switchLog = 1;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "") {
        this.switchLog = 2;
        this.four = false;
      }
      this.fourFlag = true;
    },
    //app
    appLog() {
      if (this.ruleForm.log == "") {
        this.logApp = 0;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "") {
        this.logApp = 1;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "") {
        this.logApp = 2;
        this.five = false;
      }
      this.fiveFlag = true;
    },
    //
    savation() {
      if (this.ruleForm.save == "") {
        this.saveSet = 0;
        this.ruleForm.saveLimit = "";
        this.six = true;
      } else if (this.ruleForm.save == "") {
        this.saveSet = 1;
        this.six = false;
      }
      this.sixFlag = true;
    },
    inputOnInput(e) {
      this.$forceUpdate();
    },
    //
    handler({ BMap, map }) {
      var point = new BMap.Point(104.081548, 30.652613);
      map.centerAndZoom(point, 16);
    },
    getGps(results) {
      console.log(results.Hr[0].point);
      this.gpsLocation = results.Hr[0].point;
    },
    //
    limitChange() {
      if (this.ruleForm.deviceNumLimit < this.allLimits) {
        this.$message("");
      }
    },
    //input
    inputOne() {
      this.inputOneFlag = true;
      this.currentOne = this.ruleForm.timeLimit;
    },
    inputTwo() {
      this.inputTwoFlag = true;
    },
    inputThree() {
      this.inputThreeFlag = true;
    },
    inputFour() {
      this.inputFourFlag = true;
    },
    inputFive() {
      this.inputFiveFlag = true;
    },
    inputSix() {
      this.inputSixFlag = true;
    },
    //
    decimalOne() {
      let boolean = new RegExp("^[0-9][0-9]*$").test(this.ruleForm.timeLimit);
      if (!boolean) {
        this.$message.warning("");
        this.ruleForm.timeLimit = "";
      }
    },
    decimalTwo() {
      let boolean = new RegExp("^[0-9][0-9]*$").test(this.ruleForm.videoLimit);
      if (!boolean) {
        this.$message.warning("");
        this.ruleForm.videoLimit = "";
      }
    },
    decimalThree() {
      let boolean = new RegExp("^[0-9][0-9]*$").test(this.ruleForm.appLimit);
      if (!boolean) {
        this.$message.warning("");
        this.ruleForm.appLimit = "";
      }
    },
    decimalFour() {
      let boolean = new RegExp("^[0-9][0-9]*$").test(this.ruleForm.switchLimit);
      if (!boolean) {
        this.$message.warning("");
        this.ruleForm.switchLimit = "";
      }
    },
    decimalFive() {
      let boolean = new RegExp("^[0-9][0-9]*$").test(this.ruleForm.appLogLimit);
      if (!boolean) {
        this.$message.warning("");
        this.ruleForm.appLogLimit = "";
      }
    },
    decimalSix() {
      let boolean = new RegExp("^[0-9][0-9]*$").test(this.ruleForm.saveLimit);
      if (!boolean) {
        this.$message.warning("");
        this.ruleForm.saveLimit = "";
      }
    },
    //
    async update() {
      if (this.ruleForm.deviceNumLimit < this.allLimits) {
        this.$message("");
      } else {
        let name = this.ruleForm.bname;
        let acount = this.ruleForm.mainAccount;
        this.addrDetail = this.ruleForm.address;
        let addrDetail = this.addrDetail;
        let charge = this.ruleForm.personInCharge;
        let phone = this.ruleForm.picPhone;
        let dateTime = this.ruleForm.validityTime + " " + "00:00:00";
        //
        let coStatus;
        if (this.statusFlag == true) {
          coStatus = this.ruleForm.status;
        } else {
          coStatus = this.setStatus;
        }
        let others = this.ruleForm.description;
        let limit = this.ruleForm.deviceNumLimit;
        let voice = this.ruleForm.voice;
        let see = this.ruleForm.see;
        let talk = this.ruleForm.talk;
        let rail = this.ruleForm.rail;
        let track = this.ruleForm.track;
        let broad = this.ruleForm.broad;
        let detect = this.ruleForm.detect;
        //
        let time;
        let timeLimit;
        if (this.oneFlag == true) {
          time = this.videoTime;
          timeLimit = this.ruleForm.timeLimit;
        } else {
          if (this.inputOneFlag == true) {
            timeLimit = this.ruleForm.timeLimit;
          } else {
            timeLimit = this.setVedioTimeLimit;
          }
          time = this.setVedioTimeLimitSys;
        }

        //
        let uploadVideo;
        let videoLimit;
        if (this.twoFlag == true) {
          uploadVideo = this.uploadVideo;
          videoLimit = this.ruleForm.videoLimit;
        } else {
          if (this.inputTwoFlag == true) {
            videoLimit = this.ruleForm.videoLimit;
          } else {
            videoLimit = this.setIsAutoUpload;
          }
          uploadVideo = this.setIsAutoUploadSys;
        }
        //app
        let appVideo;
        let appLimit;
        if (this.threeFlag == true) {
          appVideo = this.appVideo;
          appLimit = this.ruleForm.appLimit;
        } else {
          if (this.inputThreeFlag == true) {
            appLimit = this.ruleForm.appLimit;
          } else {
            appLimit = this.setDispatcherUploadLimit;
          }
          appVideo = this.setDispatcherUploadLimitSys;
        }
        //
        let switchLog;
        let switchLimit;
        if (this.fourFlag == true) {
          switchLog = this.switchLog;
          switchLimit = this.ruleForm.switchLimit;
        } else {
          if (this.inputFourFlag == true) {
            switchLimit = this.ruleForm.switchLimit;
          } else {
            switchLimit = this.setAppLog;
          }
          switchLog = this.setAppLogSys;
        }
        //app
        let logApp;
        let appLogLimit;
        if (this.fiveFlag == true) {
          logApp = this.logApp;
          appLogLimit = this.ruleForm.appLogLimit;
        } else {
          if (this.inputFiveFlag == true) {
            appLogLimit = this.ruleForm.appLogLimit;
          } else {
            appLogLimit = this.setDispatcherLog;
          }
          switchLog = this.setDispatcherLogSys;
        }
        //
        let saveSet;
        let saveLimit;
        if (this.sixFlag == true) {
          saveSet = this.saveSet;
          saveLimit = this.ruleForm.saveLimit;
        } else {
          if (this.inputSixFlag == true) {
            saveLimit = this.ruleForm.saveLimit;
          } else {
            saveLimit = this.setStoreTip;
          }
          saveSet = this.setStoreTipSys;
        }
        //biddid
        let didUpdate = this.did;
        let idUpdate = JSON.parse(sessionStorage.getItem("updateID"));
        let lon;
        let lat;
        if (this.changelocationflag == true) {
          lon = this.gpsLocation.lng;
          lat = this.gpsLocation.lat;
        } else {
          lon = this.setlongitude;
          lat = this.setlatitude;
        }
        let updateForm = {
          bname: name,
          mainAccount: acount,
          address: addrDetail,
          personInCharge: charge,
          picPhone: phone,
          validityTime: dateTime,
          status: coStatus,
          description: others,
          deviceNumLimit: limit,
          isCall: voice,
          vedio: see,
          talkback: talk,
          mapRange: rail,
          trail: track,
          broadcast: broad,
          listener: detect,
          vedioTimeLimitSys: time,
          vedioTimeLimit: timeLimit,
          isAutoUploadSys: uploadVideo,
          isAutoUpload: videoLimit,
          dispatcherUploadLimitSys: appVideo,
          dispatcherUploadLimit: appLimit,
          appLogSys: switchLog,
          appLog: switchLimit,
          dispatcherLogSys: logApp,
          dispatcherLog: appLogLimit,
          storeTipSys: saveSet,
          storeTip: saveLimit,
          bid: idUpdate,
          did: didUpdate,
          longitude: lon,
          latitude: lat,
        };
        const data = await this.updateCompanyAsync(updateForm);
        if (data.code == 200) {
          this.$message("");
          this.$router.push(`/Index/company`);
          this.formAsync();
        } else if (data.code == 501) {
          this.$message("");
        } else if (data.code == 502) {
          this.$message("");
        } else {
          this.$message("");
        }
      }
    },
  },
};
</script>

